var html = require('choo/html')
const Nav = require('../components/Nav');
const Footer = require('../components/footer');

module.exports = view


const organizers = [
  {name: "Jessie Braden", url:"https://twitter.com/jessiebraden?lang=en", imgSrc:"JESSIE.jpg", bio: "Jessie Braden is the co-founder and Director of Pratt Institutes Spatial Analysis and Visualization Initiative. Her work focuses on the intersection of mapping, analytics and design for telling stories with data. Jessie is an educator at heart and developed two certificate programs in GIS and design at Pratt where she also teaches in the School of Information and the Graduate Center for Planning and the Environment. Prior to Pratt, she taught in Columbia Universitys Graduate School of Architecture, Planning and Preservation for six years. Shes on Twitter every now and then: @jessiebraden."},
  {name: "Wenfei Xu", url:"http://www.wenfeixu.com/", imgSrc:"WENFEI.png", bio: "Wenfei Xu is an urban planning doctoral student at Columbia GSAPP and a spatial data scientist at CARTO. Her research and practice focuses on processes of urban change, gentrification, and neighborhood segregation using urban data science, mapping, and design strategies. Previously, she was a researcher at the MIT Civic Design Lab and Senseable Cities Lab. You can find her on Twitter @iamwfx"},
  {name: "Stephen Larrick", url:"https://twitter.com/SLarrick?ref_src=twsrc%5Egoogle%7Ctwcamp%5Eserp%7Ctwgr%5Eauthor", imgSrc:"STEPHEN.JPG", bio: "Stephen Larrick is an urban planner and open gov advocate who has spent his career working to democratize the way cities are experienced and made. He currently heads city partnership efforts at urban tech startup Stae, helping public officials better manage and collaborate with data to achieve their goals. Prior to joining Stae, Stephen founded and directed the Open Cities Team at the Sunlight Foundation and served as Director of Planning and Economic Development for the City of Central Falls, Rhode Island. Stephen received his Bachelors of Arts in Urban Studies and Political Philosophy from Brown University, and lives in Manhattan with his wife Sarah."},
  {name: "Juan Saldarriaga", url:"https://juanfrans.com/", imgSrc:"JUAN.png", bio: "I am the Senior Data & Design Researcher at the Brown Institute for Media Innovation at Columbia University. I work at the intersection of data, GIS, visualization, journalism, architecture, urbanism, and the humanities, and I teach graduate level workshops and seminars on mapping, advanced GIS and data visualization. In the past Ive worked as a research scholar at the Center for Spatial Research and the Spatial Information Design Lab, as an architectural designer for Carlos Zapata Studio and D.G.T. Architects, and as the GIS project coordinator for the Catholic Charities. You can find me on Twitter @juanfrans and on Github at github.com/juanfrans."},
  {name: "Joey Lee", url:"https://jk-lee.com", imgSrc:"JOEY.jpg", bio: "Joey Lee is a New York based designer, creative technologist, and geographer. His work explores relationships between the environment, data, and computation, often taking the form of experimental research, software, and visualization. Joey enjoys learning through making and uses design as a bridge to collaborate within and across disciplines. Joey holds a B.A. (UCLA) and M.Sc in Geography (University of British Columbia), with specialization in urban climatology and micrometeorology. He was a Mozilla Science Fellow (2015) and currently a research fellow and adjunct faculty at NYUs Interactive Telecommunications Program. www: jk-lee.com"},
]


function createOrganizer(_data){
  const {name, imgSrc, url, bio} = _data;
  return html`
    <div class="w-50-ns w-100">
      <div class="pa4-ns tc pa0">
        <img
            src="../assets/organizers/${imgSrc}"
            class="w5 dib" alt="avatar ${imgSrc}">
      </div>
      <section class="w-100 pl2-ns pr2-ns">
        <p class="b"><a class="b black link underline" href="${url}" target="_blank">${name}</a></p>
        <p>${bio}</p>
      </section>
    </div>
  `
}

function view (state, emit) {
return html`

<body class="avenir lh-copy bg-light-pink pa4-ns pa2">
  <header>
    ${state.cache(Nav, "Nav", state, emit)}
  </header>
  <main class="flex flex-column">
    <section class="flex flex-row-ns flex-column w-100 w-100-ns pa3 pa4-ns flex-wrap">
      <div class="w-100">
      <h1>About <span class="i">Data Through Design</span></h1>
      </div>
      <div class="w-100 w-50-ns pr2">
      <p>As we rapidly evolve into a society of digital natives who communicate, interact, and create in the realm
        of the ephemeral, the everyday realities of life in the city become less tangible and begin to loosen their
        hold in our psyche. The deluge and pervasiveness of data desensitizes us to the fact that, behind each data
        point is a story.</p>
      <p>Data Through Design is an annual alternative cartography exhibition held during New York Citys Open Data
        Week, an endeavor of the Mayors Office of Data Analytics. The objective of the exhibit is to enable curious
        makers such as technologist, artists, and designers to create
        novel methods of map-making, present new narrative perspectives, and develop a deeper understanding of life in
        the city using data as a medium.
      </p>
    </div>
      <div class="w-100 w-50-ns">
      <p>Each year, the exhibition takes on a new theme designed to challenge artists to consider ways in which they
        can
        surface the hidden stories that data can tell us, the absurdity underlying data, the connections that can be
        made through data, and the slippages that occurs when we use data as a heuristic for reality.</p>
      <p>We commission 8 - 10 installations based on a theme and provide each group with financial, technical, and
        design support. We encourage
        a diversity of backgrounds and media, while encouraging physical manifestations of data rather than screen
        based, digital media. Previously
        featured artists were software engineers, graphic designers, architects, urban technologists, map- makers, and
        other backgrounds all working with the citys open datasets.</p>

      <p>The exhibitions audience ranges from the civic technology community, to policy-makers, to the design
        community. Our hope is to enable a space for creative engagement with data and open new perspectives to our
        appreciation and understanding of data.</p>
        <p>- Jessie, Wenfei, Stephen, Juan, and Joey</p>
      </div>
    </section>
    <section class="flex flex-row-ns flex-column w-100 w-100-ns pa3 pa4-ns flex-wrap">
      <div class="w-100">
          <h1>Organizers</h1>
          <p>Data Through Design is an independently organized event, assembled with care and love by a group of curious strangers looking create new ways to interface with the public on issues surrounding New York City and its data.</p>
      </div>
      <div class="w-100 flex flex-row-ns flex-column flex-wrap pa0">
        ${createOrganizer(organizers[0])}
        ${createOrganizer(organizers[1])}  
      </div>
      <div class="w-100 flex flex-row-ns flex-column flex-wrap pa0">
        ${createOrganizer(organizers[2])}
        ${createOrganizer(organizers[3])}  
      </div>
      <div class="w-100 flex flex-row-ns flex-column flex-wrap pa0">
        ${createOrganizer(organizers[4])}
      </div>
    </section>
    <section class="flex flex-row-ns flex-column w-100 w-100-ns pa3 pa4-ns flex-wrap">
          <div class="w-100">
          <h1>Code Of Conduct</h1>
          <p class="i">Forked from the <a class="link black" href="https://www.mozilla.org/en-US/about/governance/policies/participation/" target="_blank">Mozilla Community Code of Conduct</a></p>
          </div>
          <div class="w-100 w-50-ns pr2">
            <p>Data Through Design events are community events intended for networking and collaboration as well as learning.</p>
            <p>We value the participation of every member of the community and want all attendees to have an enjoyable and fulfilling experience. Accordingly, all attendees are expected to show respect and courtesy to other attendees throughout the event and in interactions online associated with Data Through Design.</p> 
            <p>To make clear what is expected, everyone taking part in Data Through Design events and participantsis required to conform to the following Code of Conduct. Organizers will enforce this code throughout events, but you may also contact us directly by email at <span class="b">datathroughdesign@gmail.com</span>. All communication will be treated as confidential.</p>
            <p>Data Through Design is dedicated to providing a harassment-free experience for everyone, regardless of gender, sexual orientation, disability, physical appearance, body size, race, religion, or abilities of any kind. We do not tolerate harassment of participants in any form. Harassment includes offensive verbal comments related to gender, sexual orientation, disability, physical appearance, body size, race, religion, sexual images in public spaces, deliberate intimidation, stalking, following, harassing photography or recording, sustained disruption of talks or other events, inappropriate physical contact, and unwelcome sexual attention.</p>
          </div>
          <div class="w-100 w-50-ns pr2">
            <p>All communication should be appropriate for a professional audience including people of many different backgrounds. Sexual language and imagery is not appropriate for any event.</p>
            <p>Be kind to others. Do not insult or put down other attendees. Be careful in the words that you choose. Remember that sexist, racist, and other exclusionary jokes can be offensive to those around you.</p>
            <p>Behave professionally. Remember that harassment and sexist, racist, or exclusionary jokes are not appropriate.</p>
            <p>Attendees violating these rules may be asked to leave the event at the sole discretion of the conference organizers without a refund of any charge that may have been levied.</p>
            <p>Thank you for helping make this a welcoming, friendly event for all.</p>
          </div>
      </section>
    <section class="flex flex-row-ns flex-column w-100 w-100-ns pa3 pa4-ns flex-wrap">
          <div class="w-100">
          <h1>Contact</h1>
          <p>For inquiries, please reach out to us at:  </p>
          <p><span class="b f4">datathroughdesign@gmail.com</span></p>
          </div>
      </section>
  </main>
  ${Footer()}
</body>
`
}

