var html = require('choo/html')

const Nav = require('../components/Nav');
const Footer = require('../components/footer');


var TITLE = 'DD19 - panel'

module.exports = view

let panelists = [
  {
    name:'Ingrid Burrington',
    image:'ingrid.jpg', 
    bio:'Ingrid Burrington writes, makes maps, and tells jokes about places, politics, and the feelings people have about both. Shes the author of Networks of New York An Illustrated Field Guide to Urban Internet Infrastructure. Her work has been supported by Eyebeam, Data & Society, and the Center for Land Use Interpretation.',
    links:['http://lifewinning.com']
  },
  {
    name:'Matt Daniels',
    image:'matt.jpeg', 
    bio:'Matt Daniels is a Journalist-Engineer and Business lead/CEO at The Pudding. He first experienced Internet fame in 2014 and has been chasing that feeling ever since.',
    links:['https://twitter.com/matthew_daniels']
  },
  {
    name:'Sam Lavigne',
    image:'sam.png', 
    bio:'Sam Lavigne is an artist and educator whose work deals with data, surveillance, cops, natural language processing, and automation. He currently teaches at the School For Poetic Computation.',
    links:['https://lav.io', 'https://twitter.com/sam_lavigne']
  },
  {
    name:'David Bering-Porter',
    image:'david.jpeg', 
    bio:'David Bering-Porter is Assistant Professor of Culture and Media at the Eugene Lang College of the Liberal Arts at The New School, USA. Areas of research include film and media studies, new media theory, and the intersections of media, science, and technology.',
    links:['https://www.newschool.edu/lang/faculty/David-Bering%5EPorter/']
  }
  
]

function createArtistImage(_data){
  return html`
    <div class="pa4-ns tc pa1">
      <p class="b">${_data.name}</p>
      <img
          src="../assets/panelists/${_data.image}"
          class="h5-ns w5 dib" alt="avatar ${_data.image}">
      <p class="mw5 tl">${_data.bio}
        <ul class="list pl0 mt2">
          ${
            _data.links.map(item=>{
              return html`<li class="pl0"><a class="black" href="${item}">${item}</a></li>`
            })
          }
        </ul>
      </p>
      
    </div>

  `
}

function view (state, emit) {
  if (state.title !== TITLE) emit(state.events.DOMTITLECHANGE, TITLE)
return html`

<body class="avenir lh-copy bg-light-pink pa4-ns pa2">
  <header>
    ${state.cache(Nav, "Nav", state, emit)}
  </header>
  <main class="flex flex-column">
    <section class="flex flex-row-ns flex-column w-100 w-100-ns pa3 pa4-ns flex-wrap">
      <div class="w-100 w-100-ns">
        <h1>Panel Discussion: Everything is a Proxy</h1>
      </div>
      <div class="w-100 w-50-ns pr2">
        <p>As part of DTD, we invite speakers across disciplines to present and facilitate discussion about the theme
          <span class="i"> Everything is a Proxy</span>.</p>
          <p>Proxies in data models are used when we are unable to capture the complexity of a phenomenon of interest. It
              is difficult and quite often impossible, for example, to quantify (or even qualify) things like happiness or
              livability, yet we try to model those concepts with data like number of trees or household income. The ways
              that these proxies come together to score our cities, our neighborhoods, our friends, and ourselves can be
              dangerous and misleading, so much so that they are highlighted as a critical component of what Cathy ONeill calls,
              Weapons of Math Destruction.</p>
      </div>
      <div class="w-100 w-50-ns">
          <p>Proxies can also be fun and make you smile and act as a bridge between unconnectable things. In this series of talks, we ask our speakers
            to reflect on and share with us their stories and data projects that speak to the proxies of the
            unrepresentable value of our city and its citizens.</p>
        </div>
    </section>
    <section class="flex flex-row-ns flex-column w-100 w-100-ns pa3 pa4-ns flex-wrap">
        <div class="w-100">
          <h1>2019 Panelists</h1>
        </div>
        <div class="w-100 w-80-ns pr2">
          <p>On our panel this year, we hear from people - artists, designers, researchers, journalists - who are engaged in and/or work around data. The panel will be moderated by <a class="underline black" href="https://twitter.com/slarrick?lang=en" target="_blank">Stephen Larrick</a> who will draw upon his experiences and expertise working in city government and in civic technology to cultivate a discussion on ways in which people, organizations, and institutions are (ab)using data. We prompt our panelists and our audience to think about the power of data, who has or does not have access, and for whom data, publicly available or not, are benefitting or harming.</p>
        </div>
  
        <div class="w-100 w-80-ns pa0">
          <ul class="list flex flex-row flex-wrap justify-start w-100 pa0">
              ${createArtistImage(panelists[0])}
              ${createArtistImage(panelists[1])}
              ${createArtistImage(panelists[2])}
              ${createArtistImage(panelists[3])}
          </ul>
            
            
        </div>
  
      </section>
  </main>
  ${Footer()}
</body>
`
}